*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}


/* nav starts */
.nav_container{
    width: 100%;
    position: fixed;
    background-color: #fff;
    display: flex;
    padding: 50px 120px 20px;
    justify-content: space-between;
    z-index: 2;
}   
#logo{
    width: 50px;

}
.contact_but{
    all: unset;
    color: #7510F7;
    padding: 4px 16px;
    border-radius: 100px;
    border: 1px solid #7510F7;
}

/* nav ends */


/* photo starts */
    .photo_container{
        height: 65vh;
        background-color: rgb(202, 230, 255);
    }



/* photo ends */













/* greeting & name starts */
.name_container{
    text-align: center;
    /* background-color: #969696; */
    margin-bottom: 5%;
}



.cta_container{
    display: flex;
    justify-content: center;
    margin-top: 1%;

}

.resume_download_button,.project_button{
    all: unset;
    margin: 10px;
}
.resume_download_button{
    background: #7510F7;
    color: #fff;
    padding: 10px 26px;
    border-radius: 50px;
}
.project_button{
    color: #7510F7;
    padding: 10px 26px;
    border-radius: 50px;
    border: 1px solid #7510F7;
}


#greetings{
    font-family: outfit;
    font-weight: 100;
    font-size: 35px;

}
#myname{
    font-family:'ClashDisplay-Semibold';
    font-weight: 500;
    font-size:  60px;

}
/* greeting & name ends */



/* about starts */

.about_container{
    text-align: center;
    padding: 50px 120px; 
    background-color: #7510F7; 
    color: #ffffff;
    height: 60vh;
    z-index: 1;
    position: relative;
}
.about_container>*{
    
    margin: 20px 0px;
}



/* about ends */



/* skillset starts */


.design_card{
    margin: 0px 120px;
    padding: 80px;
    border-radius: 20px;
    text-align: center;
    display:flex ;
    flex-direction: column;
    gap: 50px;
    z-index: 1;
    position: relative;
    background: #FFF;
    box-shadow: 6px 10px 30px 0px rgba(117, 16, 247, 0.15) inset;
    filter: drop-shadow(-6px 10px 20px rgba(0, 0, 0, 0.15));
    margin-top: 80px;
}
.list_container{
    display: flex;
    text-align: center;
    justify-content: center;
    gap: 120px;

}
.list_headings{
    color: #7510F7;
    list-style: none;
    
}
ul>*{
    list-style: none;
}



.dev_card{
    margin: 30px 120px;
    padding: 80px;
    border-radius: 20px;
    text-align: center;
    display:flex ;
    flex-direction: column;
    gap: 50px;
    fill: #FFF;
box-shadow: 6px 10px 30px 0px rgba(117, 16, 247, 0.10) inset;
/* filter: drop-shadow(-6px 10px 20px rgba(0, 0, 0, 0.15)); */

}
/* skilset ends */





/* contact starts */

#contact_heading{
    text-align: center;
    font-size:xx-large ;
    color: #7510F7;
}


.contact_form{
    margin: 0px 120px;
    display: grid;
    grid-gap: 0px 30px;
    grid-template-columns: repeat(6,1fr);
    background-color: #FFF;

}
.description{
    grid-column:span 6;
}
textarea {
    resize: none;
    height: 150px;
    width: 100%;
    border-radius: 8px;
    border: 1px solid rgba(117, 16, 247, 0.40);
  }

   .name{
    grid-column:span 3;
    margin-top: 30px;
    margin-bottom: 30px;

  } 
  .email{
    grid-column:span 3;
    margin-top: 30px;
    margin-bottom: 30px;    
  }

  input{
    all: unset;
    width: 100%;
    height: 45px;
    border-radius: 8px;
    border: 1px solid rgba(117, 16, 247, 0.40);

  }

  input::placeholder{
    padding-left: 16px;
  }



  .form_button{
    all: unset;
    display: flex;
    margin: auto;
    margin-top: 30px;
    color: #ffffff;
    padding: 10px 150px;
    border-radius: 100px;
    background: #7510F7;
    border: 1px solid #7510F7;
  } 
/* contact ends */


footer{
    padding-top:30px ;
    margin-top:50px;
    height: 30vh;
    background-color: #7510F7;
    text-align: center;
    color: #FFF;

}

#footer_logo{
    width: 50px;
}
#footer_name{
    /* width: 50px; */
    margin-left: 20px;
}  
#social_link{
    margin-top: 30px;
}


@media (width<=790px) {
    .name,.email{
        grid-column:span 6;
        margin-top: 0px;
        margin-bottom: 30px;
      }
}


@media (width<=790px) {
    .name,.email{
        grid-column:span 6;
        margin-top: 0px;
        margin-bottom: 30px;
      }
      .form_button{
        padding: 10px 50px;
      }

      .nav_container{
        padding: 50px 80px ;
    }  

}
@media (width<=426px) {

      .nav_container{
        padding: 50px 40px;
    }  

}